@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-mail:before { content: ""; }

.icon-telegram:before { content: ""; }

.icon-viber:before { content: ""; }

.icon-whatsapp:before { content: ""; }

@font-face { font-family: museo-thin; src: url("fonts/museosanscyrl_300-webfont.eot"); src: url("fonts/museosanscyrl_300-webfont.eot?#iefix") format("embedded-opentype"), url("fonts/museosanscyrl_300-webfont.woff2") format("woff2"), url("fonts/museosanscyrl_300-webfont.woff") format("woff"), url("fonts/museosanscyrl_300-webfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: museo-medium; src: url("fonts/museosanscyrl_500-webfont.eot"); src: url("fonts/museosanscyrl_500-webfont.eot?#iefix") format("embedded-opentype"), url("fonts/museosanscyrl_500-webfont.woff2") format("woff2"), url("fonts/museosanscyrl_500-webfont.woff") format("woff"), url("fonts/museosanscyrl_500-webfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
@font-face { font-family: museo-bold; src: url("fonts/museosanscyrl_700-webfont.eot"); src: url("fonts/museosanscyrl_700-webfont.eot?#iefix") format("embedded-opentype"), url("fonts/museosanscyrl_700-webfont.woff2") format("woff2"), url("fonts/museosanscyrl_700-webfont.woff") format("woff"), url("fonts/museosanscyrl_700-webfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
body { font-family: "museo-thin", Arial, sans-serif; font-size: 18px; line-height: 1.4; color: #000; }

.row { position: relative; max-width: 980px; margin: 0 auto; padding: 0 10px; }
.row_center { text-align: center; }

.title-h2 { margin-bottom: 5px; text-transform: uppercase; color: #000; font-size: 30px; text-align: center; }
.title-h2_white { color: #FFF; }

.title-h3 { font-weight: normal; font-size: 18px; text-align: center; }
.title-h3_white { color: #FFF; }

.title-h4 { margin-top: 30px; margin-bottom: 20px; }
.title-h4_upper { text-transform: uppercase; }

.btn-wrap { text-align: center; }
.btn-wrap_pad { padding: 20px 0; }
.btn-wrap_abs { position: absolute; top: -63px; left: 0; right: 0; }

.btn { height: 60px; padding: 8px 55px 9px; text-align: center; color: #fff; font-size: 26px; font-family: "museo-medium", Arial, sans-serif; border-radius: 30px; text-transform: uppercase; text-decoration: none; background-clip: padding-box; transition: background-color 0.3s; background-color: #FAA620; }
.btn_green { background-color: #29b948; }
.btn_green:hover { background-color: #208f38; }
.btn_border { border: 7px solid rgba(0, 0, 0, 0.5); }
.btn_mod { padding: 13px 75px 13px; color: #000; }
.btn_mid { height: 40px; padding: 5px 15px; font-size: 24px; }

.text { margin-bottom: 20px; }
.text_center { text-align: center; }
.text_orange { color: #faa620; }
.text_white { color: #FFF; }
.text_bold { font-size: 24px; font-family: "museo-medium", Arial, sans-serif; }
.text_mg { margin-bottom: 50px; }
.text_mg_top { margin-top: 30px; margin-bottom: 0; }
.text_divider { position: relative; margin-bottom: 40px; }
.text_divider:after { content: ""; position: absolute; left: 50%; top: 100%; margin-top: 20px; transform: translateX(-50%); width: 200px; height: 3px; background-color: #2ab747; }

.section { position: relative; padding: 30px 0; }
.section_main { background-repeat: no-repeat; background-position: 50% 50%; background-size: cover; }
.section__top_bg { position: absolute; top: 0; left: 0; width: 100%; padding: 12px 0; background: rgba(0, 0, 0, 0.5); }
.section_no_pad { padding: 0; }

.main-logo { max-width: 800px; margin: 0 auto; padding: 165px 0 195px; }
.main-logo__inner { display: flex; flex-direction: row; justify-content: space-between; align-items: center; }
.main-logo span { font-size: 35px; color: #000; font-family: "museo-bold", Arial, sans-serif; text-transform: uppercase; }
.main-logo img { width: 240px; }

.reg-info { position: absolute; left: 0; right: 0; bottom: 0; text-align: center; }

.messengers { margin-bottom: 40px; }
.messengers a { display: inline-block; padding: 0 10px; color: #fff; transition: opacity 0.3s; }
.messengers a:hover { opacity: 0.6; }

.icon-viber, .icon-whatsapp, .icon-telegram { font-size: 45px; text-shadow: 3px 3px 2px rgba(0, 0, 0, 0.4); }

.icon-mail { font-size: 40px; text-shadow: 3px 3px 2px rgba(0, 0, 0, 0.4); }

.sign-up { height: 100px; padding-top: 35px; background: rgba(0, 0, 0, 0.5); color: #fff; text-align: center; }
.sign-up .upper-text { font-size: 24px; }

.upper-text { text-transform: uppercase; }

.simple-img { margin-top: 30px; max-width: 100%; width: 100%; }
.simple-img_no_mg { margin-top: 0; }

.companies__list { font-size: 0; }
.companies__list li { display: inline-block; margin-bottom: 40px; vertical-align: middle; padding: 0 40px; }
.companies__list li:before { content: ""; display: inline-block; vertical-align: middle; height: 100%; width: 0; }

.table-indicators { max-width: 610px; margin: 0 auto 30px; text-align: left; empty-cells: show; }
.table-indicators tr td { height: 50px; line-height: 50px; font-size: 25px; border-top: 2px solid #2ab747; }
.table-indicators tr td:last-child { text-align: right; }
.table-indicators tr:first-child td { border-top: none; }

.table-indicators_wide { max-width: 850px; }
.table-indicators_wide tr td:first-child { padding-right: 5px; border-top: none; text-align: right; }
.table-indicators_wide tr:last-child td { border-top: none; }

.right-align { text-align: right; }

.left-align { text-align: right; }

.cell-bold { font-family: "museo-bold", Arial, sans-serif; padding-right: 0 !important; }

.cell-bold-border { display: inline-block; max-width: 405px; width: 100%; border-top: 4px solid #2ab747; text-align: left; *zoom: 1; }
.cell-bold-border:after { content: " "; display: table; clear: both; }
.cell-bold-border span:last-child { float: right; }

.shift-to_right { padding-left: 35px; }

.w800 { max-width: 840px; }

.header { height: 60px; background: rgba(0, 0, 0, 0.5); }

.logo { float: left; width: 100px; }
.logo a { color: #fff; text-transform: uppercase; font-size: 48px; font-family: "museo-bold", Arial, sans-serif; text-decoration: none; }

.nav { float: right; }
.nav__list { font-size: 0; }
.nav__item { display: inline-block; vertical-align: top; }
.nav__link { position: relative; display: inline-block; padding: 18px 15px 14px; font-size: 20px; color: #fff; text-decoration: none; transition: background 0.3s; }
.nav__link:hover { background: rgba(255, 255, 255, 0.25); }

.params { font-size: 0; margin-right: -20px; text-align: center; }

.circle-item { display: inline-flex; vertical-align: top; flex-flow: column nowrap; justify-content: center; max-width: 182px; width: 100%; height: 182px; margin-bottom: 40px; text-align: center; color: #24BC52; border: 2px solid #24BC52; border-radius: 50%; }
.circle-item:not(:last-child) { margin-right: 20px; }
.circle-item__text { font-size: 18px; line-height: 1.2; color: #000; }
.circle-item__text_mt { margin-top: -20px; }
.circle-item__value { padding: 5px 0; color: #24bc52; line-height: 1.1; }
.circle-item__value_big { font-size: 28px; font-family: "museo-bold", Arial, sans-serif; }
.circle-item__pic { margin-bottom: 10px; }

.features { max-width: 700px; margin: 40px auto 0; }
.features_wide { max-width: 900px; margin: 40px auto 20px; }
.features__list { font-size: 0; }

.feature { position: relative; margin-bottom: 15px; color: #000; text-align: center; display: inline-block; vertical-align: top; width: 33.3%; margin-bottom: 60px; }
.feature__inner { max-width: 160px; margin: 0 auto; }
@media only screen and (max-width: 767px) { .feature { margin-bottom: 25px; } }
.feature__img { position: relative; margin-bottom: 13px; min-height: 75px; height: 1px; text-align: center; vertical-align: middle; font-size: 0; }
.feature__img_big { min-height: 130px; }
.feature__img:before { content: ""; display: inline-block; height: 100%; vertical-align: middle; width: 0; }
.feature__text { font-size: 18px; line-height: 1.3; }
.feature__text_white { color: #FFF; font-size: 25px; }

.feature_wide .feature__inner { max-width: 250px; }

.section_video { padding: 0; background-repeat: no-repeat; background-position: 50%; background-size: cover; }
.section_video.is-transparent { background-image: none !important; background-color: #000; }
.section_video.is-transparent .icon-play { visibility: hidden; opacity: 0; }

#play-video { position: relative; display: block; height: 0; padding-bottom: 56.5%; z-index: 1000; }
#play-video.hover-state .icon-play { opacity: 0.7; }
#play-video iframe { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }

.icon-play { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -30%); background-image: url("../img/icons.png"); width: 131px; height: 131px; background-position: 0px 0px; transition: opacity 0.3s; }

.members { display: flex; flex-flow: row wrap; -webkit-align-items: stretch; align-items: stretch; width: 100%; margin: 50px 0; font-size: 0; }
@media only screen and (max-width: 1023px) { .members { display: block; margin: 30px 0 -20px; } }
.members_2_col { max-width: 700px; margin: 0 auto; }
.members_2_col .member__col { width: 50%; }

.member__col { width: 33.3%; text-align: center; }
@media only screen and (max-width: 1023px) { .member__col { display: inline-block; width: 50%; margin-bottom: 60px; } }
@media only screen and (max-width: 767px) { .member__col { width: 100%; margin-bottom: 45px; } }

.member { position: relative; display: inline-block; max-width: 320px; vertical-align: top; height: 100%; }
@media only screen and (max-width: 1023px) { .member { max-width: 340px; } }
.member__photo { width: 200px; height: 200px; border-radius: 50%; margin: 0 auto 25px; overflow: hidden; }
.member__photo img { width: 100%; }
@media only screen and (max-width: 767px) { .member__photo { width: 150px; height: 150px; } }
.member__photo_mod img { transform: translateY(-9%); }
.member__name { margin-bottom: 10px; font-size: 25px; line-height: 1; color: #2ab747; }
@media only screen and (max-width: 767px) { .member__name { font-size: 26px; } }
.member__position { height: 50px; font-family: "museo-medium", Arial, sans-serif; margin-bottom: 10px; padding: 0 10px; font-size: 18px; color: #000; }
.member__mail { transition: opacity 0.15s; }
.member__mail:hover { opacity: 0.7; }
.member__info { font-size: 14px; color: #000; line-height: 1.2; max-width: 280px; margin: 0 auto; }


