.params 
	font-size: 0 
	margin-right: -20px 
	text-align: center

.circle-item
	display: inline-flex
	vertical-align: top
	flex-flow: column nowrap 
	justify-content: center
	max-width: 182px 
	width: 100% 
	height: 182px 
	margin-bottom: 40px 
	text-align: center
	color: #24BC52 
	border: 2px solid #24BC52 
	border-radius: 50%
	&:not(:last-child)
		margin-right: 20px
	&__text
		font-size: 18px
		line-height: 1.2
		color: #000 
		&_mt
			margin-top: -20px
	&__value
		padding: 5px 0
		color: #24bc52
		line-height: 1.1
		&_big
			font-size: 28px
			+mb
	&__pic
		margin-bottom: 10px 
